  white-space: normal; /* Ensure text wraps to the next line as expected */
 box-sizing: border-box; /* Ensure padding and border are included in the element's total width and height */
}


.details-box table {
 width: 600px; /* Make the table take up the full width of the container */
 table-layout: fixed; /* Ensure equal spacing across columns */
 border-collapse: collapse; /* Optional: Remove gaps between table cells */
}

.details-box th,
.details-box td {
 padding: 8px; /* Add padding for better spacing */
 text-align: left; /* Align headers and cells to the left */
 vertical-align: top; /* Align text at the top of the cell */
 overflow-wrap: break-word; /* Ensure text within cells breaks properly */
 word-wrap: break-word; /* Older property for compatibility */
 word-break: break-word; /* Standard property to break words */
}

.details-box th {
 width: 50%; /* Set a fixed width for table headers */
}

.details-box td {
 width: 70%; /* Set a fixed width for table cells */
}

/* Specific styling for the Description cell */
.details-box td.description {
 word-break: break-all; /* Break words at any character to prevent overflow */
 white-space: normal; /* Ensure text wraps normally */
 hyphens: auto; /* Optional: Add hyphens when breaking words */
}

