.truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 150px; /* Adjust as needed */
  position: relative;
  cursor: pointer;
}

.truncate:hover::after {
  content: attr(title); /* Use the title attribute as the tooltip content */
  position: absolute;
  background: #333;
  color: #fff;
  padding: 5px;
  border-radius: 4px;
  white-space: nowrap;
  top: 100%; /* Below the text */
  left: 0;
  z-index: 1000;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}


