/* Add this to your CSS file or a style block */
.dropdown-container {
  display: flex;
  align-items: flex-start;
}

.dropdown {
  flex-grow: 1;
  position: relative; /* Ensure dropdown menu is positioned relative to .dropdown */
}

.dropdown-menu {
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 1000;
}

.selected-items {
  margin-left: 20px;
  margin-top: 10px;
}



/* notification-style */

.notificationPanel {
  width: 400px;
  right: 10px; /* Ensure it's aligned to the right */
  top: 60px;
  position: fixed;
  z-index: 1000;
  background-color: white; /* Ensure it has a background to avoid overlap transparency */
  border-radius: 8px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
}

.headcontrols {
  padding: 10px;
  border-bottom: 1px solid #ddd;
}

.scrollclass {
  max-height: 500px;
  overflow-y: auto;
  padding-right: 10px; /* Ensure some padding for better scroll visibility */
}

.notificationElement {
  padding: 10px;
  border-bottom: 1px solid #eee;
  cursor: pointer;
  display: flex;
  align-items: start;
}

.notificationElement:hover {
  background-color: #f9f9f9;
}

.image-placeholder {
  width: 100px;
  height: 120px;
  background-color: #f0f0f0; /* Light grey background as a placeholder */
}


.upload-box {
  width: 100%;
  height: 100px; /* Adjust the height as needed */
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  cursor: pointer;
  
}

.file-bg {
  background-color: #e9ecef9e;
  border-radius: 2px;
}

.table-hover .hover-column {
  position: relative;
}

.table-hover .hover-column-hovered:hover {
  background-color: #e0e0e0; /* Light gray background */
  
}


